/* 建立第一個grid 定義他的column 、 row 數量 與數值，以及透過areas定義區域來供稍後section對應放入   (後續會因需求新增更多grid 如以下) */
.txt2img-first-grid{
  display: grid;
  grid-template-columns:100px;
  grid-template-rows: 1fr 1fr;

  grid-template-areas: 
  "section1"
  "section2"
;

  grid-gap: 5px;
  margin-bottom: 20px;
}

  /* 需放入的components 寫一section classname給他 而位置對應上方剛建立grid 的 areas */
  .txt2img-section1{
    grid-area: section1;
    justify-self: start;
  }
  .txt2img-section2{
    grid-area: section2;
    justify-self: start;

  }

.txt2img-second-grid{

    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr;

    grid-template-areas: 
      "section3 section4"
      "section5 section6"
    ;

  }
    .txt2img-section3{
      width: 95%;
      grid-area: section3;
    justify-self: start;
   }
    .txt2img-section4{
